/**
 * Custom styles for the My Expertise page
 *
 * @version 1.0.0
 * @package cjbarnes.github.io
 */
/**
 * Skills/technologies rows of icons.
 */
/* The skills list heading and subheadings. */
.skills {
  background: #e0d7cb; }
  .skills h2, .skills h3 {
    text-align: center;
    color: #a17167; }

/*
 * The icons containers.
 * 1. Override default list padding.
 */
.skill-list {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  /* 1 */
  list-style: none;
  text-align: center;
  /*
     * Icon wrappers.
     * 1. Use padding-top to leave space for the absolute-positioned icon.
     * 2. For icon positioning.
     */
  /* Override link hover border colours, to preserve 1px gap between items. */
  /**
     * CSS sprite icons.
     */ }
  .skill-list:before, .skill-list:after {
    content: " ";
    display: table; }
  .skill-list:after {
    clear: both; }
  .skill-list li {
    display: inline-block;
    margin: 0; }
  .skill-list a, .skill-list span {
    width: 153px;
    width: 8.5rem;
    height: 153px;
    height: 8.5rem;
    padding: 108px 9px 9px;
    padding: 6rem 0.5rem 0.5rem;
    /* 1 */
    position: relative;
    /* 2 */
    float: left;
    display: block;
    border: 1px solid #e0d7cb;
    line-height: 1em;
    background: #fff;
    color: #222;
    /*
         * Vector icon positioning.
         * 1. Overridden by the specific webfont icon.
         * 2. Horizontal centring.
         * 3. CSS sprite of non-webfont icons.
         */ }
    .skill-list a:before, .skill-list span:before {
      top: 22px;
      top: 1.2rem;
      font-size: 54px;
      font-size: 3rem;
      content: '';
      /* 1 */
      position: absolute;
      left: 50%;
      /* 2 */
      display: block;
      width: 70px;
      height: 70px;
      margin-left: -35px;
      font-family: "cj-icons";
      line-height: 3rem;
      background: transparent url('/img/skill-images.png') left top no-repeat;
      /* 3 */
      background-size: auto 70px;
      color: #a17167; }
      @media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
        .skill-list a:before, .skill-list span:before {
          background-image: url('/img/skill-images@2x.png'); } }
  .skill-list a:hover, .skill-list a:focus, .skill-list a:active {
    border-color: #e0d7cb; }
  .skill-list .html5 a:before, .skill-list .html5 span:before {
    background-position-x: 0px; }
  .skill-list .css3 a:before, .skill-list .css3 span:before {
    background-position-x: -70px; }
